.root {
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #c8c8c8;
  color: #2c2c2c;
  display: block;
  font-family: 'Open Sans';
  outline: 0;
  padding: 4px 8px;
  width: 100%;
  line-height: 1.5em;
}
  .root::-webkit-input-placeholder {
    color: #7d7d7d;
  }
  .root:-ms-input-placeholder {
    color: #7d7d7d;
  }
  .root::placeholder {
    color: #7d7d7d;
  }
  .root:hover {
    border: 1px solid #55baff;
  }
  .root:focus {
    border: 1px solid #007eff;
    color: #2c2c2c;
  }
  .root:disabled,
  .root.disabled {
    border: 1px solid rgba(206, 206, 206, 0.3);
    color: #2c2c2c;
    opacity: 0.6;
  }

.label-wrap {
  display: block;
  margin-bottom: 16px;
}

.label {
  margin-left: 2px;
  margin-bottom: 4px;
}



