:global .component-btn {
    padding: 8px;
    border-radius: 4px;
    text-align: center;
    cursor: pointer;
    max-width: 100%;
    text-decoration: none;
  }
    :global .component-btn.primary {
      border: solid 0.063rem #2c2c2c;
      background-color: #fff;
      color: #2c2c2c;
    }
    :global .component-btn.primary:hover {
        color: #0097ff;
        border-color: #0097ff;
      }
    :global .component-btn.primary:active {
        color: #007eff;
        border-color: #007eff;
      }
    :global .component-btn.primary.disabled {
        color: rgba(44, 44, 44, 0.5);
        border-color: rgba(44, 44, 44, 0.5);
      }
    :global .component-btn.secondary {
      border: solid 0.063rem #41a9ff;
      background-color: #41a9ff;
      color: #fff;
    }
    :global .component-btn.secondary:hover {
        border-color: #0097ff;
        background-color: #0097ff;
      }
    :global .component-btn.secondary:active {
        border-color: #007eff;
        background-color: #007eff;
      }
    :global .component-btn.secondary.disabled {
        border-color: rgba(65, 168, 255, 0.5);
        background-color: rgba(65, 168, 255, 0.5);
      }
    :global .component-btn.danger {
      border: solid 0.063rem #ff4164;
      background-color: #ff4164;
      color: #fff;
    }
    :global .component-btn.danger:hover {
        border-color: #ee4164;
        background-color: #ee4164;
      }
    :global .component-btn.danger:active {
        border-color: #dd4164;
        background-color: #dd4164;
      }
    :global .component-btn.danger.disabled {
        opacity: 0.5;
      }



